.greetingForm {
    margin: 31px 0 0 70px;
}

.inputAndButtonContainer {
    display: flex;

}

.error {
    height: 17px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #CC0000;
}

.input {
    display: inline-block;
    width: 372px;
    height: 36px;
    margin: 0 12px 7px 0;
    border: 1px solid #D1D1D1;
    border-radius: 5px;
    padding: 8px 0 8px 12px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
}

.input:focus {

}

.errorInput {
    border: 1px solid #CC0000;
}

.button {
    width: 78px;
    height: 36px;
    border: none;
    border-radius: 3px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #FFFFFF;
    background: #0066CC;

}

.button:focus {
    opacity: 1;
}

.button:active {
    opacity: 1;
}

.button:disabled {
    opacity: 0.5;
}

.text {
    height: 17px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    margin-bottom: 9px;
    color: rgba(0, 0, 0, 0.5);
}

.greeting {
    width: 144px;
    height: 17px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    /* identical to box height */


    color: #000000;
}
